import React, { useState, useEffect } from 'react';
import instance from '../../axios';


function GuardForm(props) {
    const csrftoken = window.CSRF_TOKEN;
    const [name, setName] = useState("");
    const [type, setType] = useState("");
    const [duration, setDuration] = useState("");
    const id = props.id
    const servId = Number(props.servId)


    function updateIf(){
            if (props.name){
                setName(props.name)
        }
    }

    useEffect(() =>{
        updateIf()
    },[props.name])
    
   /* ------------ SAVE / UPDATE DEPARTMENT -------------*/

    function saveGuard(e) {
        e.preventDefault();
        if (props.id){
            const postData = {
                "id": id,
                "name": name,
                "type": type,
                "duration_hs": duration,
                "service_id": servId
            };
            instance.put('/guard/', postData).then((response) => { props.after(response.data) })
            setName('')
            setType('')
            setDuration('')
        } else {
            const postData = {
                "name": name,
                "type": type,
                "duration_hs": duration,
                "service_id": servId
            };
            console.log(postData)
            instance.post('/guard/', postData).then((response) => { props.after(response.data) })
            setName('')
            setType('')
            setDuration('')
        };
    }

    return (
        <div className="guard-form">
            <form method="post" className="form-input" onSubmit={saveGuard}>
                <input name="csrfmiddlewaretoken" value={csrftoken} hidden />
                <input className="Inputext" type="text" name="username" value={name} placeHolder="Nombre de servicio" onChange={(e) => setName(e.target.value)} required />
                <div className="selected">
                
                <select required onChange={(e) => setType(e.target.value)}>
                    <option value="" disabled hidden selected>Selecione tipo</option>
                    <option value="active">Activa</option>
                    <option value="pasive">Pasiva</option>
                </select>
               
               
                <select required onChange={(e) => setDuration(e.target.value)}>
                    <option value="" disabled hidden selected>Selecione duración</option>
                    <option value="24">24hs</option>
                    <option value="24">12hs</option>
                    <option value="24">8hs</option>
                </select>
                </div>
               
                <button type="submit" className="btn btn-outline-success"><i class="bi bi-check-lg"></i></button>
            </form>
        </div>
    )
}

export default GuardForm;